.world-clock {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  grid-gap: 1em;
  gap: 1em;
  height: 80%;
  width: 100%;
  font-size: calc(0.1em + 1vw);
  color: #2b2b85;
}

.world-clock .current-location {
  position: relative;
  padding: 0.5em 1em;
  background: none;
  border: none;
  text-align: center;
}

.world-clock button.chose-location {
  background: none;
  position: absolute;
  right: 0;
  border: none;
  margin: .3em .3em;
  color: #2b2b85;
  transition: .3s;
}

.world-clock button.chose-location.rotate {
  -webkit-transform: rotatez(90deg);
          transform: rotatez(90deg);
}

.world-clock .show-location-menu {
  left: 0 !important;
}

.world-clock .hide-location-menu {
  left: -15em !important;
}

.world-clock .Location-menu::-webkit-scrollbar {
  width: 0.5em;
}

.world-clock .Location-menu::-webkit-scrollbar-track {
  background: none;
  border-bottom-right-radius: 4em;
}

.world-clock .Location-menu::-webkit-scrollbar-thumb {
  background-color: #8390a9b9;
  border-radius: 4em;
  width: 0.5em;
}

.world-clock .Location-menu::-webkit-scrollbar-thumb:hover {
  background-color: #5f697a;
}

.world-clock .Location-menu {
  position: absolute;
  display: -ms-grid;
  display: grid;
  top: 0;
  background: #3500981a;
  border-bottom-right-radius: 1em;
  color: #2b2b85;
  grid-gap: 5em;
  gap: 5em;
  width: 10em;
  height: 50%;
  z-index: 1;
  overflow-y: scroll;
  scroll-behavior: smooth;
  scrollbar-color: black;
  box-shadow: inset -2px -1px 5px #8390a9, 2px 2px 6px 1px #deeee9;
  transition: 0.6s;
  -webkit-animation: ease-in-out;
          animation: ease-in-out;
}

.world-clock .Location-menu ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-gap: 1em;
  gap: 1em;
  width: 100%;
}

.world-clock .Location-menu button, .world-clock .Location-menu button input {
  background: none;
  cursor: pointer;
  border: none;
  color: #2b2b85;
  width: 100%;
  text-align: center;
  padding: .3em;
  font-size: calc(0.1em + 1vw);
}

.world-clock .Location-menu button:hover {
  font-size: calc(0.2em + 1vw);
}

.world-clock .clock-container {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: visible;
}

.world-clock .clock-container .clock {
  min-height: 15em;
  min-width: 15em;
  border-radius: 50%;
  box-shadow: 6px 6px 20px 7px #acd5c9, inset 11px 9px 20px 20px #b7dcd0, -9px -1px 20px 0px #b9dcd1, inset -7px 3px 9px 12px #ffffff, -15px -9px 12px 3px #ffffff;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

.world-clock .clock-container .hour,
.world-clock .clock-container .min,
.world-clock .clock-container .sec {
  z-index: 1;
  display: flex;
  position: absolute;
  justify-content: center;
}

.world-clock .clock-container .sec {
  width: 1em;
  height: 12em;
  border-radius: 1em;
}

.world-clock .clock-container .min {
  width: 1em;
  height: 9em;
  border-radius: 1em;
}

.world-clock .clock-container .hour {
  width: 1em;
  height: 5em;
  border-radius: 1em;
}

.world-clock .clock-container .hour::before,
.world-clock .clock-container .sec::before,
.world-clock .clock-container .min::before {
  z-index: 1;
  content: "";
  background-color: #350098;
  position: absolute;
  border-radius: 1em;
}

.world-clock .clock-container .hour::before {
  width: .5em;
  height: 2.5em;
}

.world-clock .clock-container .min::before {
  width: .5em;
  height: 4.5em;
}

.world-clock .clock-container .sec::before {
  width: .5em;
  height: 6em;
}

.world-clock .date {
  width: 100%;
  height: 1em;
  margin: 0.5em 0em;
  text-align: center;
  color: #2b2b85;
}
